.text-center {
  text-align: center; }

@keyframes glow {
  0% { }
  50% {
    transform: scale(1.4); }
  100% { } }

@keyframes blink {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

